.productcard{
    width: 350px;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
    border-radius: 5px;
    margin: 10px;
    padding: 10px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.productcard img {
    min-width: 100%;
    height: 250px;
    margin: 10px 0px;
}
.productcard .name{
    margin: 10px 0px;
}
.productcard p{
    font-size: 18px;
    font-weight: 500;
}
.productcard .action{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 10px 0px;
}
.productcard button{
    border: 0;
    border-radius: 5px;
    font-size: 16px;
    color: #FFFFFF;
    background-color: #1c59ae;
    padding: 7px 10px;
    cursor: pointer;
    min-width: 150px;
}
.productcard button:hover{
    background-color: #103f80;
}
.productcard .remove{
    background-color: #ac0d14;
}
.productcard .remove:hover{
    background-color: #8e090f;
}